.header-prt {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.link-container {
  width: 375px;
  justify-content: center;
  align-items: center;
  gap: 50px;
  margin-top: 40px;
  margin-bottom: 40px;
  display: flex;
}

.link-container .link {
  color: gray;
  font-size: 32px;
  text-decoration: none;
  transition: transform .3s ease-in-out;
}

.link-container .link:hover {
  transform: scale(1.1);
}

.input-div {
  width: 376px;
  justify-content: center;
  align-items: center;
  margin-bottom: 100px;
  display: flex;
}

.input {
  width: 320px;
  height: 60px;
  color: #474545;
  text-align: center;
  border: 2px solid gray;
  outline: none;
  padding: 0 20px;
  font-size: 40px;
  display: block;
}

.input::-ms-input-placeholder {
  text-align: center;
  color: #9b9393;
}

.input::placeholder {
  text-align: center;
  color: #9b9393;
}

.btn-div {
  gap: 50px;
  margin-top: 40px;
  display: flex;
}

.btn {
  width: 60px;
  height: 60px;
  color: #474545;
  cursor: pointer;
  border: 2px solid gray;
  border-left: none;
  outline: none;
  font-size: 18px;
  transition: all .3s ease-in-out;
  display: block;
}

.btn:hover {
  color: #fff;
  background-color: #716d6d;
}

.li-btn {
  width: 150px;
  height: 40px;
  color: #474545;
  cursor: pointer;
  background-color: #e7a146;
  border: none;
  border-radius: 4px;
  transition: all .3s ease-in-out;
}

.li-btn:hover {
  color: #000;
  background-color: #e38d1d;
  transform: scale(1.02);
}

.js-list {
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  gap: 40px;
  margin: 0;
  padding: 0;
  display: flex;
}

.js-list-item {
  outline: 1px solid gray;
  flex-direction: column;
  align-items: center;
  margin: 0 0 50px;
  padding: 20px;
  display: flex;
}

.js-list-item-open {
  width: 375px;
  background-color: #fff;
  flex-direction: column;
  align-items: center;
  margin: 0;
  padding: 40px;
  display: flex;
}

a {
  color: orange;
  text-decoration: none;
  transition: color .3s ease-in-out;
}

.js-list-item-open h2 {
  margin-top: 50px;
}

.js-list-item-open h3 {
  align-self: flex-start;
}

h2, h3 {
  margin: 5px 0;
}

a:hover {
  color: gray;
}

.notification {
  color: #000;
  text-align: center;
  z-index: 9999;
  background-color: #e7a146;
  border-radius: 4px;
  padding: 10px;
  font-size: 28px;
  display: none;
  position: fixed;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

@media screen and (max-width: 375px) {
  .js-list-item-img {
    width: 300px;
  }

  .js-list-item-open {
    max-width: 300px;
  }

  .input {
    max-width: 200px;
  }

  .input::-ms-input-placeholder {
    font-size: 22px;
  }

  .input::placeholder {
    font-size: 22px;
  }

  .link-container {
    max-width: 310px;
    gap: 30px;
    margin-top: 40px;
    margin-bottom: 40px;
  }

  .link-container .link {
    font-size: 20px;
  }

  .btn-div {
    gap: 10px;
  }
}

@media screen and (min-width: 768px) {
  .js-list-item-img, .js-list-item-open {
    width: 500px;
  }

  .link-container {
    width: 100%;
    gap: 40px;
    margin-top: 60px;
    margin-bottom: 60px;
  }
}

@media screen and (min-width: 1440px) {
  .js-list-item-img, .input {
    width: 600px;
  }

  .link-container {
    gap: 50px;
    margin-top: 100px;
    margin-bottom: 100px;
  }
}

.basicLightbox {
  width: 100%;
  height: 100vh;
  opacity: .01;
  z-index: 1000;
  will-change: opacity;
  background: rgba(0, 0, 0, .8);
  justify-content: center;
  align-items: center;
  transition: opacity .4s;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.basicLightbox--visible {
  opacity: 1;
}

.basicLightbox__placeholder {
  max-width: 100%;
  z-index: 1;
  will-change: transform;
  transition: transform .4s;
  transform: scale(.9);
}

.basicLightbox__placeholder > iframe:first-child:last-child, .basicLightbox__placeholder > img:first-child:last-child, .basicLightbox__placeholder > video:first-child:last-child {
  max-width: 95%;
  max-height: 95%;
  margin: auto;
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.basicLightbox__placeholder > iframe:first-child:last-child, .basicLightbox__placeholder > video:first-child:last-child {
  pointer-events: auto;
}

.basicLightbox__placeholder > img:first-child:last-child, .basicLightbox__placeholder > video:first-child:last-child {
  width: auto;
  height: auto;
}

.basicLightbox--iframe .basicLightbox__placeholder, .basicLightbox--img .basicLightbox__placeholder, .basicLightbox--video .basicLightbox__placeholder {
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.basicLightbox--visible .basicLightbox__placeholder {
  transform: scale(1);
}

